body {
  font-family: 'Raleway', sans-serif;
  background-color: #ffffff;
  color: #000000;
  font-size: 16px; }

.parallax {
  min-height: 20em;
  background: transparent; }
  @media screen and (min-device-width: 768px) and (max-device-width: 1024px) {
    .parallax {
      min-height: 35em; } }
  @media screen and (min-width: 1024px) {
    .parallax {
      min-height: 40em; } }
  @media screen and (min-width: 1300px) {
    .parallax {
      min-height: 45em; } }

header {
  color: #ffffff;
  padding: 3em;
  text-align: center;
  font-family: 'Rock Salt', cursive; }
  header.header__intro {
    text-align: right; }
    header.header__intro h2 {
      font-family: 'Raleway', sans-serif; }

.sidebar {
  max-height: 20em;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #ffffff;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px; }
  .sidebar a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 1.5em;
    color: #818181;
    display: block;
    transition: 0.3s; }
    .sidebar a:hover {
      color: #444;
      font-style: italic; }
  .sidebar .social {
    display: flex;
    flex-direction: row; }
  @media screen and (min-device-width: 768px) and (max-device-width: 1024px) {
    .sidebar {
      max-height: 15em; } }
  @media screen and (min-width: 1024px) {
    .sidebar {
      max-height: 20em; } }
  @media screen and (min-width: 1300px) {
    .sidebar {
      max-height: 30em; } }

.closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px; }

.openbtn {
  font-size: 20px;
  cursor: pointer;
  background-color: #ffffff;
  color: #000000;
  padding: 10px 15px;
  border: none;
  border-radius: 50%; }
  .openbtn:hover {
    background-color: #444; }

#toggle {
  transition: margin-left .5s;
  padding: 16px; }
